$mainPink: #df42b1;

.schedule {
	width: 100%;

	&__title {
		text-align: center;

		h4 {
			color: $mainPink;
			font-weight: 400;
		}
	}

	&__nav {
		margin-top: 70px;
		display: flex;
		justify-content: center;
		margin-bottom: 30px;
		position: relative;

		ul {
			display: flex;
			position: relative;

			.schedule__nav-item {
				padding: 20px 40px;
				text-align: center;
				color: #fff;
				background-color: #1F2371;
				cursor: pointer;
				transition: background .15s ease-in-out;

				&:hover {
					background: lighten($color: #1F2371, $amount: 3%);
				}

				&:nth-child(1) {
					border-right: 1px rgba($color: #fff, $alpha: .3) solid;
					border-radius: 10px 0 0 10px;
				}

				&:nth-child(3) {
					border-left: 1px rgba($color: #fff, $alpha: .3) solid;
					border-radius: 0 10px 10px 0;
				}

				&.active {
					background: #5D5E8D;
				}

				span {
					text-transform: uppercase;
					font-size: 18px;
					font-weight: 600;
					letter-spacing: 1px;
				}

				p {
					margin: 0;
					font-size: 14px;
					font-weight: 500;
					letter-spacing: 1px;
				}
			}
		}
	}

	&__list {
		width: 100%;
		display: block;
		transition: all 1s ease-in-out;


		&-item {
			display: flex;
			border: 1px rgba($color: #fff, $alpha: .2) solid;
			border-radius: 10px;
			padding: 30px;
			transition: border-color .3s ease-in-out;
			margin-top: 20px;

			&:hover {
				border-color: #df42b1;
			}
		}


	}

	&__left-side {
		display: flex;
		align-items: center;
		max-width: 42%;
		width: 42%;
		padding-right: 15px;

		img {
			width: 90px;
			height: 90px;
			border-radius: 100px;
		}

		&-text {
			margin-left: 20px;

			p {
				color: #9293bc;

				span {
					color: #fff;
				}
			}
		}
	}

	&__right-side {
		max-width: 58%;
		width: 58%;
		display: flex;
		align-items: center;

		&-date {
			display: flex;
			flex-direction: column;
			flex: 1;

			p {
				color: #fff;
				display: flex;
				align-items: center;

				svg {
					width: 15px;
					height: 15px;
					fill: $mainPink;
					margin-right: 15px;
				}
			}
		}
	}

	&__download-btn {
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 50px;

		a {
			cursor: pointer;
			align-items: center;
			padding: 12px 25px;
			text-transform: uppercase;
			font-size: 14px;
			font-weight: 500 !important;
			color: #fff;
			border: solid 3px transparent;
			border-radius: 50px;
			background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(130deg, rgba(223, 66, 177, 1) 5%, rgba(115, 75, 187, 1) 95%);
			;
			background-origin: border-box;
			background-clip: content-box, border-box;
			box-shadow: 2px 1000px 1px #151853 inset;
			transition: box-shadow .15s ease-in-out;

			&:hover {
				box-shadow: none;
			}
		}
	}
}

@media (max-width: 991px) {
	.schedule {
		&__list-item {
			display: flex;
			flex-direction: column;
		}

		&__left-side {
			flex-direction: column;
			max-width: 100%;
			width: 100%;

			&-text {
				margin: 20px 0;
				text-align: center;
			}
		}

		&__right-side {
			flex-direction: column;
			max-width: 100%;
			width: 100%;

			&-date {
				p {
					display: flex;
					flex-direction: column;
					text-align: center;
					justify-content: center;
	
					svg {
						margin: 0 0 10px 0;
					}
				}
			}
		}
	}
}

@media (max-width: 575px) {
	.schedule {
		&__nav {
			ul {
				flex-direction: column;
				width: 100%;
				.schedule__nav-item {
					border: none;
					border-radius: 10px;
					
					&:nth-child(1) {
						border-right: none;
						border-radius: 10px;
						margin-bottom: 10px;
					}
		
					&:nth-child(3) {
						border-left: none;
						border-radius: 10px;
						margin-top: 10px;
					}
				}
			}

			&-item {
				padding: 15px !important;
				span {
					font-size: 16px !important;
				}
				p {
					font-size: 12px !important;
				}
			}
		}

		&__list-item {
			padding: 15px;
		}

		&__left-side {
			img {
				width: 75px;
				height: 75px;
			}
			h6 {
				font-size: 16px;
			}
			p {
				font-size: 14px;
			}
		}

		&__right-side {
			p {
				font-size: 14px;
			}
			&-date {
				margin-bottom: 15px;
				align-items: start;
				p {
					display: flex;
					flex-direction: row;
					text-align: start;
					justify-content: center;
					
	
					svg {
						position: relative;
						top: 5px;
						margin-right: 10px;
					}
				}
			}
		}
	}
}